import React from 'react';
import {Container, Row, Col, Form} from "react-bootstrap";
import NumberFormat from 'react-number-format';

import './App.css';

export default class FareCalculator extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            distance: false,
            drivingTime: false,
            waitTime: false,
            additionalStops: false,
            extraPassengers: false,
            distanceCost: 0,
            drivingCost: 0,
            waitingCost: 0,
            extraCost: 0,
            subTotal: 0,
            tax: 0,
            total: 0,
        };
        this.handleInputChange = this.handleInputChange.bind(this);
    }

    componentDidMount(){
        document.title = "Fare Calculator";
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;

        this.setState({
            [name]: value
        }, () => this.updateValues());
    }

    updateValues() {
        this.calculateDistanceCost();
        this.calculateDriveTime();
        this.calculateWaitTime();
        this.calculateAdditionalCosts();
    }

    updateTotals() {
        this.calculateSubTotal();
        this.calculateTax();
    }

    calculateDistanceCost() {
        var distanceCost = 0;
        if (this.state.distance > 5) {
            distanceCost = this.state.distance * 1.4;
        }
        else {
            distanceCost = 7;
        }
        this.setState({ distanceCost: distanceCost }, () => this.updateTotals());
    }

    calculateDriveTime() {
        this.setState({ drivingCost: this.state.drivingTime * 0.25 }, () => this.updateTotals());
    }

    calculateWaitTime() {
        this.setState({ waitingCost: this.state.waitTime * 0.50}, () => this.updateTotals());
    }

    calculateAdditionalCosts() {
        this.setState({ extraCost: (this.state.additionalStops * 2) + (this.state.extraPassengers * 2)}, () => this.updateTotals());
    }

    calculateSubTotal() {
        this.setState({ subTotal: this.state.distanceCost + this.state.drivingCost + this.state.waitingCost + this.state.extraCost}, () => this.calculateTax());
    }

    calculateTax() {
        this.setState({ tax: this.state.subTotal * 0.15}, () => this.calculateTotal());
    }

    calculateTotal() {
        this.setState({total: this.state.subTotal + this.state.tax});
    }

    render() {
        console.log(this.state);
        return(
            <div>
                <br/>
                <br/>
                <br/>
                <Container>
                    <Form>
                        <Form.Row>
                            <Col>
                                <Form.Label>Distance (km)</Form.Label>
                                <Form.Control type="number" name="distance" onChange={this.handleInputChange} />
                            </Col>
                            <Col>
                                <Form.Label>Distance + Base Cost:</Form.Label>
                                <Form.Control type="number" readOnly value={this.state.distanceCost} onChange={this.handleInputChange} />
                                <Form.Text>$1.40 per km, or $7.00 if under 5km distance</Form.Text>
                            </Col>
                        </Form.Row>
                        <Form.Row>
                            <Col>
                                <Form.Label>Driving time (minutes)</Form.Label>
                                <Form.Control type="number" name="drivingTime" onChange={this.handleInputChange} />
                            </Col>
                            <Col>
                                <Form.Label>Driving cost ($0.25/min):</Form.Label>
                                <Form.Control type="number" readOnly value={this.state.drivingCost} onChange={this.handleInputChange} />
                            </Col>
                        </Form.Row>
                        <Form.Row>
                            <Col>
                                <Form.Label>Wait time (minutes)</Form.Label>
                                <Form.Control type="number" name="waitTime" onChange={this.handleInputChange} />
                            </Col>
                            <Col>
                                <Form.Label>Waiting cost ($0.50/min):</Form.Label>
                                <Form.Control type="number" readOnly value={this.state.waitingCost} onChange={this.handleInputChange} />
                            </Col>
                        </Form.Row>
                        <Form.Row>
                            <Col>
                                <Form.Label>Additional stops</Form.Label>
                                <Form.Control type="number" name="additionalStops" onChange={this.handleInputChange} />
                                <Form.Label>Extra passengers</Form.Label>
                                <Form.Control type="number" name="extraPassengers" onChange={this.handleInputChange} />
                            </Col>
                            <Col>
                                <Form.Label>Additional Costs:</Form.Label>
                                <Form.Control type="number" readOnly value={this.state.extraCost} onChange={this.handleInputChange} />
                                <Form.Text>$2 per stop/extra passenger</Form.Text>
                            </Col>
                        </Form.Row>
                    </Form>
                </Container>
                <br/>
                <br/>
                <br/>
                <Container>
                    <Row>
                        <Col>
                            <Form.Label class={"subtotal"}>Subtotal: </Form.Label>
                        </Col>
                        <Col>
                            <NumberFormat value={this.state.subTotal} displayType={'text'} prefix={'$'} decimalScale={'2'} class={"subtotal"} />
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Form.Label class={"subtotal"}>Tax: </Form.Label>
                        </Col>
                        <Col>
                            <NumberFormat value={this.state.tax} displayType={'text'} prefix={'$'} decimalScale={'2'} class={"subtotal"} />
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Form.Label class={"total"}>Total: </Form.Label>
                        </Col>
                        <Col>
                            <NumberFormat value={this.state.total} displayType={'text'} prefix={'$'} decimalScale={'2'} class={"total"}/>
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}
